window.doReady = function () {
	$('#formSubmitBtn').on('click', function (e) {
		//
		var loading = weui.loading('提交中...');

		setTimeout(function () {
			loading.hide(function () {
				console.log('`loading` has been hidden');
			});

			httpPost('/pay',
				null,
				function (response) {
					if (response['body'] && response['body']['fillCount']) {
						var fillCount = response['body']['fillCount'];
						sessionStorage.FillCount = fillCount;

						var url = getUrl('/success.html' + '?timestamp=' + new Date().getTime());
						window.location.replace(url);
					}
				});

		}, 666);

	});

	//weui.form.checkIfBlur('#reg_form');
	// })
}